import { Component, OnInit } from '@angular/core';
import { Data } from '../data-type';
import { StreetsService } from '../streets.service';

@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.scss']
})
export class RegistrationComponent implements OnInit {

	time = ['10:00 - 13:00', '13:00 - 16:00', '16:00 - 19:00'];
	timeItem:boolean = false;
	selectedTime: any;
	submitted = false;
	flatPopup = false;
	items: Data[];
	congratulation = false;
  house: Data;
  numbers;
	
  constructor(
		private streetsService: StreetsService
  ) {}

  ngOnInit(): void {
    this.validationAge();
    this.geStreets();
 	}

  chooseTime(): void {
    this.timeItem = !this.timeItem;
  	document.getElementById("timeText").classList.toggle("select-arrow-active");
  }

  geStreets(): void {
    this.streetsService.getData().subscribe(items => this.items = items);
  }

  geHouse(): void {
    const numberValue = (<HTMLInputElement>document.getElementById("adress__street")).value;
    this.streetsService.getHouse(numberValue).subscribe(house => this.house = house);
    this.numbers = this.house.number;
    this.numbers.sort(function(a, b){return a - b});
  }


	onSelect(i: any): void {
  	this.selectedTime = this.time[i];
  	document.getElementById("timeText").innerHTML = this.selectedTime;
  	(<HTMLInputElement>document.getElementById("inputTime")).value = this.selectedTime;
		this.chooseTime();
	}

	validationAge(): void {
	  function setInputFilter(textbox, inputFilter) {
  		["input", "keydown", "keyup", "mousedown", "mouseup", "select", "contextmenu", "drop"].forEach(function(event) {
    		textbox.addEventListener(event, function() {
      		if (inputFilter(this.value)) {
        		this.oldValue = this.value;
        		this.oldSelectionStart = this.selectionStart;
        		this.oldSelectionEnd = this.selectionEnd;
      		} else if (this.hasOwnProperty("oldValue")) {
        	  this.value = this.oldValue;
        		this.setSelectionRange(this.oldSelectionStart, this.oldSelectionEnd);
      		} else {
        	  this.value = "";
      		}
    		});
 		  });
	  }

	  setInputFilter(document.getElementById("age"), function(value) {
      return /^\d*$/.test(value) && (value === "" || parseInt(value) <= 100);
	  });
	
    setInputFilter(document.getElementById("phone"), function(value) {
      return /^\+[380]*([0-9]{2}[0-9]{2}|)*$/.test(value);
	  });
	}


	valueCheck(): void {

		var name = (<HTMLInputElement>document.getElementById("name"));
    var age = (<HTMLInputElement>document.getElementById("age"));
    var phone = (<HTMLInputElement>document.getElementById("phone"));
    var street = (<HTMLInputElement>document.getElementById("adress__street"));
    var house = (<HTMLInputElement>document.getElementById("adress__house"));
    var flat = (<HTMLInputElement>document.getElementById("adress__flat"));
   	var inputTime = (<HTMLInputElement>document.getElementById("inputTime"));

   	if (name.value == "") {
      name.style.borderColor = "red";
      event.preventDefault();
    } else {
      name.style.borderColor = "#afb0af";
    }
    if (age.value < "18") {      
        age.style.borderColor = "red";
        event.preventDefault();        
    } else {
      age.style.borderColor = "#afb0af";
    }
    if (phone.value == "") {
      phone.style.borderColor = "red";
      event.preventDefault();
    } else {
      phone.style.borderColor = "#afb0af";
    }
    if (street.value == "") {
      street.style.borderColor = "red";
      event.preventDefault();
    } else {
      street.style.borderColor = "#afb0af";
    }
    if (house.value == "") {
      house.style.borderColor = "red";
      event.preventDefault();
    } else {
      house.style.borderColor = "#afb0af";
    }
    if (flat.value == "") {
      flat.style.borderColor = "red";
      this.flatPopup = true;
      event.preventDefault();
    } else {
      flat.style.borderColor = "#afb0af";
      this.flatPopup = false;
    }
    if (inputTime.value == "") {
      (<HTMLInputElement>document.getElementById("timeText")).style.borderColor = "red";
      event.preventDefault();
    } else {
      (<HTMLInputElement>document.getElementById("timeText")).style.borderColor = "#afb0af";
    }
    if (name.value !== "" && age.value >= "18" && phone.value !== "" && street.value !== "" && house.value !== "" && flat.value !== "" && inputTime.value !== "") {
      this.congratulation = true;
    }
	}
}
