import { Component, OnInit } from '@angular/core';
import { StreetsService } from '../streets.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

import { Promo } from '../promokod-type';
import { postData } from '../post-type';

import { MainComponent } from '../main/main.component';

@Component({
  selector: 'app-promokod',
  templateUrl: './promokod.component.html',
  styleUrls: ['./promokod.component.scss']
})
export class PromokodComponent implements OnInit {

  machine = './assets/img/promokod/machine.png';
  beer = './assets/img/promokod/beer.png';
  misic = './assets/img/promokod/misic.png';
  plusicon = './assets/img/promokod/+-copy.png';
  play = './assets/img/promokod/play.png';
  //kod;
  // kodarray;
  postCode: postData;
  //result;

  constructor(
  		private mainComponent: MainComponent,
      private streetsService: StreetsService,
      private router: Router
  	) { }

  ngOnInit(): void {
   // this.getPromo();
  }

  getPromo(): void {
    this.streetsService.postPromocode(this.postCode).subscribe(response => {
      console.log(response.status);
      if (response.status === 201) {
        this.router.navigate(['/registration']);  
      }
    });
  }

  checkPromokod(): void {
    this.postCode = new postData();
    this.postCode.code = "";
    this.postCode.code = (<HTMLInputElement>document.getElementById('quantity')).value;
    this.getPromo();

   /* let promokod = (<HTMLInputElement>document.getElementById('quantity')).value;

    let promokodCheck = this.kod.includes(promokod);
    
    if (promokodCheck == true) {
     this.router.navigate(['/registration']);
    }*/    
  }
  onEnter(value: string): void {
    this.postCode = new postData();
    this.postCode.code = "";
    this.postCode.code = value;
    this.getPromo();
  }
}
