import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';

import { Data } from './data-type';
import { postData } from './post-type';
import { DATA } from './data';

import { Promo } from './promokod-type';
import { PROMO } from './promokod';

@Injectable({
  providedIn: 'root'
})
export class StreetsService {

  constructor(
  		 private http: HttpClient
  ) { }

  private posttUrlCode = 'http://37.57.31.220:3000/api/promo-code/use';


 	getData(): Observable<Data[]> {
  		return of(DATA);
	}

	getHouse(number: any): Observable<Data> {    
    	return of(DATA.find(house => house.name === number));
  	}

  postPromocode(postCode: postData): Observable<any> {
    return this.http.post(this.posttUrlCode, postCode, {observe: 'response'});
  }  

}
