import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RulesComponent } from './promokod/rules/rules.component';
import { PromokodComponent } from './promokod/promokod.component';
import { FullpriceComponent } from './fullprice/fullprice.component';
import { RegistrationComponent } from './registration/registration.component';


const routes: Routes = [
	{ path: '', component: PromokodComponent },
	{ path: 'rules', component: RulesComponent },
	{ path: 'fullprice', component: FullpriceComponent },
	{ path: 'registration', component: RegistrationComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
